a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit; }

header {
  width: 100%;
  height: 60px;
  background-color: #ffffff;
  display: flex; }
  header nav {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0; }
    header nav .homeBtnDiv {
      position: absolute;
      left: 0;
      padding-left: 10px; }
    header nav ul {
      padding: 0;
      margin: 0;
      position: absolute;
      display: flex;
      right: 0; }
      header nav ul li {
        padding: 0;
        list-style: none; }
      header nav ul img {
        height: auto;
        width: 3em; }
      header nav ul .name {
        font-weight: 600;
        margin-right: 20px; }
      header nav ul .profileBtnDiv {
        padding-right: 10px; }
        header nav ul .profileBtnDiv .profileBtn {
          border-radius: 3px; }
